import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom';
import { getDoc, doc } from 'firebase/firestore';
import { db } from '../../../config/firebase';

// components
import { Loader } from '../../Loader/Loader';
// styles
import './SubmittedForm.css';

export const SubmittedForm = () => {

    const [dataForm, setDataForm] = useState({});
    const [isLoading, setIsLoading] = useState(true);

    const {idForm} = useParams();

    const getData = async (id) => {
        const querySnapshot = await getDoc(doc(db,"submittedForm",id));
        setDataForm({id: querySnapshot.id, ...querySnapshot.data()});
        setIsLoading(false);
    }
    
      useEffect(() => {
        setIsLoading(true);
        getData(idForm);
      }, [idForm]);

      const capitalize = (str) => {
        return str.charAt(0).toUpperCase() + str.slice(1);
      }

  return (
    <main className="main__container">
      <div className="banner"></div>
      <div className="submitted__container">
          <h1>Formulario enviado</h1>
          <h2>Sus respuestas son: </h2>
          {
              isLoading ? <div className="loader__container"><Loader /></div> : <div className="form__container--submitted">
              <label className="label__input--submitted">Nombre completo </label>
              <input className="form__input--submitted" value={dataForm?.full_name} disabled/>
              <label className="label__input--submitted">Email </label>
              <input className="form__input--submitted" value={dataForm?.email} disabled/>
              <label className="label__input--submitted">Fecha de nacimiento </label>
              <input className="form__input--submitted" value={dataForm?.birth_date} disabled/>
              <label className="label__input--submitted">Pas de origen </label>
              <input className="form__input--submitted" value={capitalize(dataForm?.country_of_origin)} disabled/>
              <div className='terms__container--submitted'>
                <label className="label__input--submitted">Trminos y condiciones </label>
                <input className="form__input--submitted" type="checkbox" checked={dataForm?.terms_and_conditions} disabled/>
              </div>
          </div>
          }

          <Link className='link__btn' to="/">Volver al formulario</Link>
      </div>
    </main>
  )
}